.center{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    text-align: center;
}

@media (max-width: 800px){
    .newscard{
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 98%;
        padding: 5px;
        -webkit-align-items: center;
                align-items: center;
        margin: 10px auto;

    }
    .newscard img{
        width: 100%;
        height: 100%;
        padding-bottom: 20px;
    }
    .newsText{
        float: left;
    }
   
}

@keyframes marquee {
    0% {text-indent: 100%;}
    100% {text-indent: -100%;}
}
@-webkit-keyframes marquee{
    0% {text-indent: 100%;}
    100% {text-indent: -100%;}
}
.marquee{
    overflow: hidden;
    white-space: nowrap;
    animation: marquee 17s linear infinite;
    -webkit-animation:  marquee 17s linear infinite;
} 

marquee{
    margin-top: 5px;
    margin-bottom: 10px;
    padding-top: 30px;
    height: 5px;
    background-color: #222;
    padding-bottom: 30px;
    font-size: 20px;
    color:white;
    box-shadow: 0px 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    line-height: 5px;
    border-radius:5px;
    font-family: "Roboto", "sans-serif";
    font-weight: bold;
    display: block;
}
@media (max-width: 800px){
 marquee{
    padding-top: 0px;
    padding-bottom: 0px;
    /* margin-bottom: 5px; */
    visibility: hidden;
 }
}

