@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap');

.newscard{
    
    display:flex;
    width:98%;
    box-shadow: 0px 2px 5px 0 rgba(0,0,0,0.16),0 2px 10px 0 rgba(0,0,0,0.12);
    padding:10px;
    margin-bottom: 25px;
    border-radius:5px;

}
.newscard img{
    object-fit: cover;
    width: 320px;
    height: 268px;
}
.newsText{
  padding: 0 0 0 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
 
}
.author{
    font-size: 12px;
    font-weight: 300;
    line-height: 20px;
    color: black;

}
/* .author a{
    color: black;
    text-decoration: none;
} */

.lowernewstext{
    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */
    flex: 1;
    padding: 10px 0;
}
.readmore{
    font-size: 12px;
    font-weight: 400;
    padding-top:10px;
}
/* .readmore a{
    color: black;
    text-decoration: none;
} */
.title{
    font-size: 20px;
    line-height: 27px;
    font-family: "Roboto", "sans-serif";
   font-weight: bold;
}
.muted{
    font-size: 100;
}
.description{
    font-size: 15px;
    line-height: 22px;
    color:black;
    font-weight: normal;
}
