.center{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    text-align: center;
}

@media (max-width: 800px){
    .newscard{
        flex-direction: column;
        width: 98%;
        padding: 5px;
        align-items: center;
        margin: 10px auto;

    }
    .newscard img{
        width: 100%;
        height: 100%;
        padding-bottom: 20px;
    }
    .newsText{
        float: left;
    }
   
}
